<template>
  <div id="login">
    <div id="login_box">
      <el-form ref="ruleForm" :model="ruleForm" :rules="rules">
        <img src="./logo.svg" alt />
        <el-form-item prop="mobile">
          <el-input v-model="ruleForm.mobile" placeholder="请输入手机号">
            <i slot="prefix" class="iconfont icon-shouji"></i>
          </el-input>
        </el-form-item>
        <el-form-item prop="code">
          <el-input v-model="ruleForm.code" placeholder="请输入密码">
            <i slot="prefix" class="iconfont icon-mima"></i>
          </el-input>
        </el-form-item>
        <el-form-item prop="check" style="text-align:left;">
          <el-checkbox v-model="ruleForm.check" style="margin-right:10px;"></el-checkbox>
          <span>我已阅读并同意用户协议和隐私条款</span>
        </el-form-item>
        <el-form-item>
          <el-button :disabled="LoginFlag" :loading="LoginFlag" type="primary" style="width:100%" @click="login()" >登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>

import '@/assets/js/gt.js'
import '@/assets/iconfont/iconfont.css'

export default {
  data () {
    var checkObj = (rule, value, callback) => {
      if (value) {
        callback()
      } else {
        callback(new Error('请勾选协议'))
      }
    }

    return {
      Obj: null,
      LoginFlag: false,
      ruleForm: {
        mobile: '13356781234',
        code: '246810',
        check: true
      },
      rules: {
        mobile: [
          { required: true, message: '请填写手机号', trigger: 'blur' },
          { pattern: /^1[35789]\d{9}$/, message: '手机号码格式不对' }
        ],
        code: [{ required: true, message: '密码不能为空', trigger: 'blur' }],
        check: [{ validator: checkObj, trigger: 'blur' }]
      }
    }
  },

  methods: {
    login () {
      this.$refs.ruleForm.validate(v => {
        if (this.Obj) {
          return this.Obj.verify()
        }
        this.LoginFlag = true
        this.$http.get(
          `/captchas/${this.ruleForm.mobile}`
        )
          .then(res => {
            let { data } = res.data
            // 请检测data的数据结构， 保证data.gt, data.challenge, data.success有值
            window.initGeetest({
            // 以下配置参数来自服务端 SDK
              gt: data.gt,
              challenge: data.challenge,
              offline: !data.success,
              new_captcha: true,
              product: 'bind'
            }, (captchaObj) => {
            // 这里可以调用验证实例 captchaObj 的实例方法
              captchaObj.onReady(() => {
                // 验证码ready之后才能调用verify方法显示验证码
                this.Obj = captchaObj
                this.LoginFlag = false
                captchaObj.verify()
              }).onSuccess(() => {
                // your code
                this.TolLogin(v)
              }).onError(() => {
                // your code
              })
            })
          })
      })
    },
    TolLogin (v) {
      if (v) {
        this.$message({
          message: '恭喜你，登录成功',
          type: 'success',
          duration: 1500
        })
        this.$http
          .post(
            '/authorizations',
            this.ruleForm
          )
          .then(res => {
            let userData = res.data.data
            window.sessionStorage.setItem(
              'userinfo',
              JSON.stringify(userData)
            )
            this.$router.push({ name: 'home' })
          })
      } else {
        this.$message({
          message: '用户名或密码错误',
          type: 'error',
          duration: 1500
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
#login {
  width: 100%;
  height: 100%;
  background-color: #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("./login_bg.jpg");
  background-size: cover;
  #login_box {
    width: 400px;
    height: 340px;
    background-color: #fff;
    border-radius: 5px;
    text-align: center;
    .el-form {
      width: 75%;
      margin: 20px auto;
      img {
        width: 50%;
        margin-bottom: 10px;
      }
    }
  }
}
</style>
