import Vue from 'vue'
import VueRouter from 'vue-router'

import login from '../views/Login/login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/home',
    name: 'home',
    component: () => import('@/views/home'),
    redirect: '/home/index',
    children: [
      { path: '/home/index', name: 'home1', component: () => import('@/views/home/homeIndex.vue') },
      { path: '/article', name: 'article', component: () => import('@/views/article/article.vue') },
      { path: '/amend:mid', name: 'amend', component: () => import('@/views/amend/amend.vue') },
      { path: '/account', name: 'account', component: () => import('@/views/account/account.vue') },
      { path: '/material', name: 'material', component: () => import('@/views/material/material.vue') },
      { path: '/fans', name: 'fans', component: () => import('@/views/fans/fans.vue') },
      { path: '/articleAdd', name: 'home2', component: () => import('@/views/article/articleAdd.vue') }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  // ...
  if (!window.sessionStorage.getItem('userinfo') && to.path !== '/login') {
    return next('/login')
  }
  next()
})

export default router
